.hero {
  width: 80%;
  height: 80vh;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand h2 span {
  color: #5222d0;
}

.navbar-brand h2 {
  color: #ec615b;
  font-weight: 800;
}

.nav-link {
  font-weight: 500;
  font-size: 18px;
}

.left {
  width: 50%;
}

.left h1 {
  font-size: 70px;
  font-weight: 800;
  line-height: 100px;
}
.left span {
  color: #5222d0;
}

.left .line {
  font-size: 18px;
  width: 600px;
  line-height: 40px;
  font-weight: 800;
  color: rgba(94, 92, 92, 0.877);
  margin-top: 30px;
  margin-bottom: 50px;
}

.left .welcome {
  font-weight: 600;
}

.btns {
  width: 350px;
  display: flex;
  justify-content: space-between;
}

.hire {
  background: #5222d0;
  color: white;
  font-weight: 700;
}

.project {
  border: 2px solid #ec615b;
  color: #ec615b;
  font-weight: 700;
}

.right {
  width: 50%;
}

.form-check {
  position: absolute;
  right: 80px;
  top: 50%;
  transform: rotate(90deg);
  width: 100px;
  height: 30px;
}

.form-check input {
  width: 70px !important;
  height: 35px;
}

.About {
  width: 100%;
  height: 100vh;
  display: flex;
  padding: 0;
  justify-content: space-between;
  align-items: center;
}

.left2 {
  width: 50%;
  height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  /* background: red; */
  background: url(../img/about.png);
  background-position: right;
  background-repeat: no-repeat;
  background-size: 900px;
  margin-bottom: 70px;
}

.right2 {
  width: 50%;
  height: 100%;
  align-items: left;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding-left: 150px;
  /* background: chartreuse; */
}

.right2 h1 {
  color: #5222d0;
  font-size: 55px;
  font-weight: 800;
}

.right2 span {
  color: #ec615b;
}

.right2 p {
  width: 450px;
  font-size: 18px;
  margin-top: 40px;
  line-height: 40px;
  font-weight: 500;
}

.Skills {
  width: 100%;
  height: 65vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 400px;
}

.Skills h1 {
  color: #5222d0;
  font-weight: 600;
  font-size: 55px;
  margin-bottom: 100px;
}

.Skills h1 span {
  color: #ec615b;
}

.skills-main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.left3 {
  width: 50%;
  height: 100%;
  /* margin: 40px 40px; */
}


.left3 ul{
  list-style: none;
  /* background: red; */
  position: relative;
  margin: 0;
  padding: 0;
}

.left3 ul::before{
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 0px;
  height: 100%;
  border: 2px dashed rgb(225, 193, 255);
}

.left3 ul li{
  margin:  80px 60px;
  /* background: cyan; */
  position: relative;
  line-height: 32px;
  font-weight: 500;
}

.left3 ul li::before{
  content: '';
  position: absolute;
  left: -60px;
  top: 0;
  width: 0px;
  height: 100%;
  border: 2px solid violet;
}


.left3 ul li span{
  content: '';
  position: absolute;
  left: -60px;
  top: 0;
  width: 0px;
  height: 100%;
  border: 2px solid violet;
}

.left3 ul li span::before, .left3 ul li span::after{
  content: '';
  width: 20px;
  height: 20px;
  border: 2px solid coral;
  position: absolute;
  border-radius: 50%;
  left: -10px;
  background: linear-gradient(to right top, #7f4cff, #ec615b);
}

.left3 ul li span::before{
  top: -15px;
}
.left3 ul li span::after{

  top: 100%;
}


.left3 ul .title{
  background: -webkit-linear-gradient(#5222d0, #ec615b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 20px;
  font-weight: 800;
  margin-bottom: 10px;
}


/* .left3 img {
  width: 50%;
} */

.right3 {
  width: 50%;
  height: 100%;
}

.right3 img {
  width: 120%;
  height: 100%;
}

.Contact {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.left4 {
  width: 50%;
  height: 70%;
}

.left4 img {
  width: 100%;
}

.right4 {
  width: 50%;
  height: 70%;
}

.Contact h1 {
  color: #5222d0;
  font-weight: 600;
  font-size: 55px;
  text-align: center;
}

.Contact h1 span {
  color: #ec615b;
}

.c-info {
  width: 450px;
  display: flex;
  justify-content: space-between;
  margin: auto;
  margin-top: 50px;
}

.email {
  font-size: 22px;
  font-weight: 700;
  color: #5222d0;
}

.info {
  font-size: 18px;
  font-weight: 500;
}

form {
  width: 400px;
  height: 500px;
  margin: auto;
}

.send {
  height: 40px;
  background: #5222d0;
  color: white;
  width: 150px;
  font-weight: 600;
  margin-left: 120px;
  margin-top: 25px;
}

input[type="text"] {
  width: 100%;
  height: 35px;
  border: 1px solid black;
  border-radius: 10px;
  padding-left: 10px;
}

input[type="email"] {
  width: 48%;
  height: 35px;
  border: 1px solid black;
  border-radius: 10px;
  padding-left: 10px;
  margin-top: 25px;
}

input[type="phone"] {
  width: 48%;
  height: 35px;
  border: 1px solid black;
  border-radius: 10px;
  padding-left: 10px;
  float: right;
  margin-top: 25px;
}

textarea {
  margin-top: 25px;
  width: 100%;
  height: 100px;
  border: 1px solid black;
  border-radius: 10px;
  padding-left: 10px;
}

.projects{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.projects img{
  width: 40%;
  margin-top: 50px;
}


.projects h1 {
  color: #5222d0;
  font-weight: 600;
  font-size: 55px;
  text-align: center;
}

.projects h1 span {
  color: #ec615b;
}


.project-main{
  display: grid;
  grid-template-columns: 48% 48%;
  grid-gap: 4%;
  margin-top: 100px;
  height: auto;
  padding-bottom: 200px;
}

.card1{
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: left;
  flex-direction: column;
}

.card1 img{
  width: 100%;
}

.desc{
  margin-top: 20px;
}

.desc h3{
  color: #5222d0;
  font-weight: 700;
}

.desc p{
  color: #ec615b;
}

.back{
  background: linear-gradient(#5222d0, #ec615b);
  font-weight: 600;
}




/* ---------------------MEDIA QUERY FOR MOBILES----------------- */

