  }

  .skills-main {
    display: flex;
    flex-direction: column-reverse;
    width: 100%;
    /* margin-top: 100px; */
  }

  .skills-main h1 {
    text-align: center;
  }

  .left3 {
    width: 100%;
    padding-left: 50px;
  }

  .right3 {
    width: 100%;
  }

  .right3 img{width: 100%;}

  .Contact {
    flex-direction: column;
    padding-top: 300px;
  }
  .left4 {
    width: 100%;
  }

  .right4 {
    width: 100%;
  }

  .c-info {
    width: 90%;
    margin-bottom: 50px;
  }

  .info{
      font-size: 16px;
  }

  form {
    width: 90%;
    margin-top: 80px;
  }

  input[type="text"] {
